import React from 'react'
import Card from '@material-ui/core/Card'
import CardActionArea from '@material-ui/core/CardActionArea'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import CardMedia from '@material-ui/core/CardMedia'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

import pythonBanner from '../../static/banner-python.jpg'
import goBanner from '../../static/banner-go.jpg'
import javascriptBanner from '../../static/banner-javascript.jpg'
import webBanner from '../../static/banner-web.jpg'

const styles = {
  width: '30%',
  maxWidth: '345px',
  minWidth: '280px',
  marginBottom: '2rem'
}

const getImage = language => {
  language = language.toLowerCase()

  switch (language) {
    case 'python':
      return pythonBanner
    case 'go':
      return goBanner
    case 'javascript':
      return javascriptBanner
    default:
      return webBanner
  }
}

const RepositoryCard = ({ repo }) => (
  <Card style={styles} className='wow bounceIn'>
    <CardActionArea>
      <CardMedia
        component='img'
        alt='Programming language image'
        style={{ objectFit: 'cover' }}
        height='140'
        image={getImage(repo.language)}
        title='Programming language image'
      />
      <CardContent>
        <Typography color='primary' component='h3' variant='h6' align='center'>
          { repo.name.toUpperCase() }
        </Typography>
        <Typography component='p'>
          { repo.description }
        </Typography>
      </CardContent>
    </CardActionArea>
    <CardActions style={{ display: 'flex', justifyContent: 'center' }}>
      <Button
        size='small'
        color='primary'
        component='a'
        href={repo.html_url}
        target='_blank'
      >
        Repositorio
      </Button>
      { repo.homepage && <Button size='small' color='secondary' component='a' href={repo.homepage} target='_blank'>Demo</Button> }
    </CardActions>
  </Card>
)

export default RepositoryCard
