import React, { Component, Fragment } from 'react'
import CssBaseline from '@material-ui/core/CssBaseline'
import WOW from 'wow.js'

import MainHeader from '../components/MainHeader'
import MainFooter from '../components/MainFooter'
import Repositories from '../components/Repositories'
import AboutMe from '../components/AboutMe'
import Spinner from '../components/Spinner'
import SocialButton from '../components/SocialButton'

import GetRepos from '../services/get-repositories'

import './App.css'

class App extends Component {
  constructor () {
    super()
    this.state = {
      repos: null
    }
    this.getRepos = this.getRepos.bind(this)
  }

  componentWillMount () {
    const wow = new WOW()
    wow.init()
  }

  componentDidMount () {
    this.getRepos()
  }

  getRepos () {
    const URL = 'https://api.github.com/users/orlmonteverde/repos'
    GetRepos(URL)
      .then(result => {
        let repositories = result.data
          .filter(repo => !repo.fork && repo.homepage)
        this.setState({ repos: repositories })
      })
      .catch(err => console.log(err))
  }

  render () {
    return (
      <Fragment>
        <MainHeader />
        <CssBaseline />
        <AboutMe />
        {
          this.state.repos
            ? <Repositories repos={this.state.repos} />
            : <Spinner />
        }
        <MainFooter />
        <SocialButton />
      </Fragment>
    )
  }
}

export default App
